import { css } from 'lit';

export const styles = css`[part=headers]{background:var(--item-background)}[part=selected-indicator]{background:var(--indicator-color)}[part=start-scroll-button],[part=end-scroll-button]{background:var(--button-background)}[part=start-scroll-button]:hover,[part=start-scroll-button]:focus,[part=end-scroll-button]:hover,[part=end-scroll-button]:focus{background:var(--button-hover-background)}igc-icon-button::part(base){color:var(--button-color)}igc-icon-button::part(base):hover,igc-icon-button::part(base):focus-within{color:var(--button-hover-color)}igc-icon-button[disabled]::part(base){color:var(--button-disabled-color)}`;
